<template>
  <div id="app">
    <!-- 路由站位符 -->
    <router-view class="App-box"></router-view>
    <!-- 标签栏渲染 -->
    <div class="lwh_foot" v-if="this.$store.state.lwh_flag">
      <van-tabbar v-model="active" active-color="#eb6100" inactive-color="#666">
        <van-tabbar-item
          v-for="(item, index) in lwh_list"
          :key="index"
          :to="item.path"
          :icon="index == active ? item.icons : item.icon"
          >{{ item.cont }}</van-tabbar-item
        >
      </van-tabbar>
    </div>
    <!-- 点击留言 -->
    <div class="lwh_fixed" @click="lwh_Aflag = true" v-drag>
      <van-icon name="comment" color="#fff" size="0.3rem" />
    </div>
    <!-- 留言框弹出 -->
    <v-leave v-show="lwh_Aflag" @lwh_leave="lwh_leaves"></v-leave>
  </div>
</template>

<script>
import VLeave from "./views/lwh_views/lwh_leave";
export default {
  data() {
    return {
      // 标签拦下标
      active: JSON.parse(window.sessionStorage.getItem("lwh_active")) || 0,
      // 留言组件显示条件
      lwh_Aflag: false,
      // 标签栏内容渲染
      lwh_list: [
        {
          cont: "首页",
          icon:
            "data:image/png;base64,",
          icons: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          path: "/lwh_main",
        },
        {
          cont: "课程",
          icon:
            "data:image/png;base64,",
          icons: "https://wap.365msmk.com/img/course-active.cb51bc51.png",
          path: "/lwh_course",
        },
        {
          cont: "约课",
          icon:
            "data:image/png;base64,",
          icons: "https://wap.365msmk.com/img/study-active.a0a234d2.png",
          path: "/lwh_york",
        },
        {
          cont: "练习",
          icon:
            "data:image/png;base64,",
          icons:
            "data:image/png;base64,",
          path: "/lwh_exercise",
        },
        {
          cont: "我的",
          icon:
            "data:image/png;base64,",
          icons:
            "data:image/png;base64,",
          path: "/lwh_my",
        },
      ],
    };
  },
  components: {
    VLeave,
  },
  methods: {
    // 子组件传值隐藏
    lwh_leaves() {
      this.lwh_Aflag = false;
    },
    // 路由跳转保存到本地
    lwh_btn_active(num) {
      window.sessionStorage.setItem("lwh_active", num);
      this.active = JSON.parse(window.sessionStorage.getItem("lwh_active"));
    },
  },
  watch: {
    // 路由跳转保存到本地
    $route(to) {
      if (to.path == "/lwh_main") {
        this.lwh_btn_active(0);
      } else if (to.path == "/lwh_course") {
        this.lwh_btn_active(1);
      } else if (to.path == "/lwh_york") {
        this.lwh_btn_active(2);
      } else if (to.path == "/lwh_exercise") {
        this.lwh_btn_active(3);
      } else {
        this.lwh_btn_active(4);
      }
    },
  },
};
</script>
<style lang="scss" >
html,
body,
#app {
  width: 100%;
  height: 100%;
  font-size: 0.16rem;
}
* {
  margin: 0;
  padding: 0;
}
#app {
  display: flex;
  flex-direction: column;
}
.App-box {
  width: 100%;
  flex: 1;
  overflow: hidden;
}
#app > .lwh_foot {
  width: 100%;
  height: 0.53rem;
}
.lwh_fixed {
  width: 0.62rem;
  height: 0.62rem;
  position: fixed;
  border-radius: 50%;
  background: #007aff;
  bottom: 0.75rem;
  right: 0.1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000;
}
.lwh_leave {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100001;
  background: #f2f3f5;
}
</style>
